.blog__section{
    display: flex;
    align-items: flex-start;
    justify-content: center;
    flex-direction: column;
    padding: 5rem;
    gap: 2rem;

}
.blog__section >h1{
    font-weight: 800;
font-size: 50px;
line-height: 66px;
/* identical to box height, or 93% */

letter-spacing: -0.03em;

color: #000000;
}
.blog__section--middle{
    display: flex;
    align-items: flex-start;
    justify-content: space-around;
    background-color: white;
    padding-left: 2rem;
    gap: 18rem;
}
.blog__section--middle-left{
    display: flex;
    flex-direction: column;
    gap: 1rem;
    align-items: center;
    justify-content: center;
}
.blog__section--middle-left> h1{
    width: 524px;

font-weight: 700;
font-size: 29px;
line-height: 36px;

margin-bottom: 3rem;

letter-spacing: -0.02em;

color: #000000;

}
.blog__section--middle-left h4{
    font-weight: 700;
font-size: 16px;
line-height: 36px;
margin-top: 2rem;


letter-spacing: 0.07em;
text-transform: uppercase;

color: #000000;

}
.blog--image img{
    width: 30rem;
    height: auto;
}
.blog__section--bottom{
    display: flex;
    align-items: flex-start;
    justify-content: space-around;
    gap: 6rem;
}

.bogs__items{
    display: flex;
    align-items: flex-start;
    justify-content: center;
    flex-direction: column;
    background-color: white;
    width: 386px;
}
.bogs__items h4{
    font-weight: 700;
font-size: 16px;
line-height: 36px;
letter-spacing: 0.07em;
text-transform: uppercase;
padding: 1rem;
}

.bogs__items h1{
    font-weight: 700;
font-size: 26px;
line-height: 36px;

letter-spacing: -0.02em;
padding: 1rem;
margin-bottom: 2.4rem;
}
.bogs__items img{
    width: 382px;
height: 202px;
}

@media screen and (max-width: 976px){
    .blog__section--middle{
        gap: 0rem;
        flex-direction: column;
        width: 100%;
        align-items: center;
    }
    .blog__section--bottom {
        flex-wrap: wrap;
        align-items: flex-start;
        justify-content: flex-start;
        gap: 1rem;
    }
    .bogs__items {
        display: flex;
        align-items: center;
        justify-content: flex-start;
        flex-direction: column;
        background-color: white;
        width: 100%;
        margin: 0rem;
        
    }
    .bogs__items img {
        width: 100%;
        height: auto;
        
    }
    .blog__section{
        gap: 0rem;
        justify-content: flex-start;
        margin-top: 0rem;
        padding: 2rem;
    }
    .blog__section >h1{
        font-size: 30px;
    }
    .blog__section--middle-left{
        gap: 0rem;
    }
    .blog__section--middle-left> h1{
        font-size:22px;
        margin-bottom: 1rem;
        width: 100%;
    }
    .bogs__items h1 {
        font-weight: 700;
        font-size: 20px;
        line-height: 0px;
        letter-spacing: -0.02em;
       padding: 0rem;
       margin-left: 0rem;
    }
        
}

@media screen and (max-width: 600px){
    .blog__section >h1 {
        font-weight: 800;
        font-size: 36px;
        line-height: 66px;
        letter-spacing: -0.03em;
        color: #000000;
    }
    .blog__section{
        gap: 0rem;
        padding: 2rem;
    }
    .blog__section--middle-left> h1 {
        margin-bottom: 0rem;
        font-size: 1rem;
       
    }
    .blog__section--middle {
        gap: 2rem;
        flex-direction: column;
        width: 100%;
    }
    .bogs__items{
        width: 100%;
        margin-top: 1rem;
    }
    .blog__section--bottom{
        gap: 1rem;
    }
    .blog--image img {
        width: 100%;
        height: auto;
    }
}

@media screen and (max-width: 400px) {
    
    .blog__section--middle-left> h1 {
       font-size: 12px;
       line-height: 20px;
    }
    .bogs__items {
        width: 100%;
        

    }
    .blog--image img {
        width: 100%;
        height: auto;
    }
    .blog__section--bottom{
        margin-top: 1rem;
        gap: 1rem;
    }
    .bogs__items h1 {
        font-weight: 700;
        font-size: 15px;
        line-height: 28px;
        letter-spacing: -0.02em;
       padding: 0rem;
       margin-left: 0rem;
    }
    .button-blogs>span{
        font-size: 12px;
    }
    .blog__section--middle {
        gap: 1rem;
    }
    .blog__section >h1{
        font-size: 16px;
        line-height: 25px;
    }
    .bogs__items h4{
        line-height: 0px;
        font-size: 16px;
    }
}
@media screen and (min-width: 980px) {
    .blog__section--middle{
        gap: 4rem;
    }
    .blog--image img{
        width: 100%;
        height: auto;
    }
    .blog__section--middle-left> h1{
        width: 100%;
    }
    .button-blogs >span{
        font-size: 1rem;
       
    }
    .blog__section--bottom{
        flex-wrap: wrap;
        align-items:center;
        justify-content: center;
        flex-direction: row;
        gap: 2rem;
    }



}
