.state__section{
    display: flex;
    align-items: center;
    justify-content: space-between;
    background-color: #3734A9;
    padding: 1.5rem 0rem 1.5rem 5rem;
    
}
.state__Section-item p{
    font-size: 13px;
line-height: 31px;
width: 6rem;


letter-spacing: -0.02em;

color: #FFFFFF;
}
.state__Section-item h1{
    font-weight: 800;
font-size: 72px;
line-height: 72px;
letter-spacing: -0.02em;

color: #FFFFFF;
    
}
.state__section-1 >img{
    height: 1rem;
}
.state__section-1{
    display: flex;
    gap: 1rem;
}
.state__section-1 span{
    color: white;
}
.state__Section-item{
    display: flex;
    align-items: flex-start;
    justify-content: flex-start;
    gap: 1.5rem;
    flex-direction: column;
}
@media screen and (max-width: 976px){
    .state__Section-item h1{
        font-size: 2rem;
    }
    .state__Section-item {
        gap: 1rem;

    }
    .state__Section-item img{
        height: 5rem;
    }
    .state__section-1 >img {
        height: 1rem;
}
}
@media screen and (max-width: 600px){
    .state__Section-item h1{
        font-size: 2rem;
        line-height: 27px;
    }
    .state__Section-item {
        gap: 1rem;

    }
    .state__Section-item img{
        height: 3rem;
    }
    .state__section-1 >img {
        height: 1rem;
    }
    .state__section{
        padding-left: 1rem;
    }
}
@media screen and (max-width: 400px){
    .state__Section-item h1{
        font-size: 12px;
        line-height: 20px;
    }
    .state__Section-item {
        gap: .7rem;

    }
    .state__Section-item img{
        height: 2rem;
    }
    .state__section-1 >img {
        height: 8px;
    }
    .state__section{
        padding-left: .8rem;
    }
    .state__section-1 span {
        color: white;
        font-size: 8px;
    }
    .state__section-1 {
        display: flex;
        gap: .2rem;
        /* flex-direction: column; */
}
.state__Section-item p {
    font-size: 11px;
    line-height: 22px;
    width: 7rem;
    letter-spacing: -0.02em;
    color: #FFFFFF;
}
}


