.balance__section--items{
    display: flex;
    align-items: flex-start;
    justify-content: flex-start;
    flex-direction: column;
    gap: 2rem;
}
.balance__section{
    display: flex;
    align-items: center;
    justify-content: space-evenly;
}


@media screen and (max-width: 976px){
    .balance__section{
        padding: 2rem;
    }
    .balance__section--right img{
        width: 100%;
        height: auto;
    }
    .rectangular {
        padding: 1rem;
        width: 100%;
    }
    .balance__section--items {
        gap: 2rem;
    }
    .blog__section--middle {
        width: 100%;
    }
}

@media screen and (max-width: 600px){
    .balance__section{
        padding: 2rem;
        flex-direction: column;
    }
    .balance__section--items {
        gap: 2rem;
        margin-left: 0rem;
    }
    .balance__section--right{
        margin-bottom: -5rem;
    }

}

@media screen and (max-width: 400px){
    .balance__section{
        align-items: flex-start;
        gap: 0rem;
        justify-content: flex-start;
        padding: 0rem;
    }
    .content__section-right {
        width: 91%;
    }
    hr{
        width: 100%;
    }
    
    .rectangular {
        padding: 1rem;
        width: 100%;
    }
}

@media screen and (min-width: 980px){
    .balance__section--right img{
        width: 100%;
    }
    .balance__section--items{
        width: 100%;
    }
    .balance__section{
        justify-content: center;
        align-items:center;
        padding: 0rem;
        padding-left: 4rem;
    }
}
