.content__section{
    display: flex;
    align-items: flex-start;
    justify-content: center;
    margin-top: 8rem;
    gap: 4rem;


}
.content__section-left{
   
    background-image: url('./logo/content.svg');
    background-repeat: no-repeat, repeat;
    height: 30rem;
    width: auto;
    border-radius: 1rem;
    padding: 3rem;
}
.chatting--content img{
    height: 33rem;
    width: auto;

    
}

.content__section-right{
    display: flex;
    align-items: flex-start;
    justify-content: flex-start;
    flex-direction: column;
    width: 490px;

}
.content__section-right h4{
    font-size: 14px;
}
.content__section-right h1{
    font-size: 50px;
}
.content__section-right p{
    font-size: 18px;
    line-height: 30px;


letter-spacing: -0.02em;
}
.content__section-rightBottom{
    display: flex;
    flex-direction: column;
    gap: 1rem;
}
.content__section-rightBottom img{
    height: 1rem;
    width: auto;
}
hr{
    width: 100%;
    margin-top: 1rem;
    margin-bottom: 1rem;
    height: 2px;
    background: #C4C4C4;
}


@media screen and (max-width: 976px){
.content__section-right {
    display: flex;
    align-items: flex-start;
    justify-content: flex-start;
    flex-direction: column;
    width: 100%;

}
.content__section{
    gap: 2rem;
    padding-left: 2rem;
    display: flex;
    flex-direction: column;
}
.content__section-right h1 {
    font-size: 20px;
}

.content__section-right p {
    font-size: 14px;
    line-height: 1.4rem;
}
.content__section-rightBottom {
    
    gap: .3rem;
}
.content__section-right h4 {
    
    margin-top: 2rem;
}
.chatting--content img {
    width: 100%;
    height: auto;
}
}


@media screen and (max-width: 600px){
    .content__section-left {
    
    background-repeat: no-repeat, repeat;
   height: 15rem;
   width: 19rem;
    border-radius: 1rem;
    padding: 3rem;
} 
.chatting--content img {
    width: 100%;
    height: auto;
}
hr{
    width: 100%;
}
.balance__section--items {
    gap: 3rem;
    margin-left: 4rem;
}


   
}
@media screen and (max-width: 400px){
    hr{
        width: 100%;
    }
    .content__section{
        margin-top: 0rem;
        padding: 1rem;
    }
    .content__section-right h1 {
        font-size: 16px;
    }
    .content__section-right p {
        font-size: 12px;
        line-height: 1.4rem;
    }
   .content__section-rightBottom>div>span{
    font-size: 12px;
   }
   .content__section-rightBottom img {
    height: 15px;
    width: auto;
}
.content__section-right {
    width: 100%;
    padding-left: 1rem;
}

}
@media screen and (min-width: 980px){
    .content__section{
        gap: 0rem;
        padding: 0rem;
        padding-left: 4rem;
    }
    .content__section-right h1 {
        font-size: 30px;
    }
    .content__section-left{
        width: 100%;
        height: auto;
        padding: 0rem;
    }
    .content__section-right{
        width: 100%;
    }
}