.faq__top{
    display: flex;
    align-items: flex-end;
    justify-content: space-between;
}
.faq__top >h1{
    margin-left: -50rem;
    width: 31rem;
    font-weight: 900;
    font-size: 50px;
    line-height: 86px;
    letter-spacing: -0.03em;

}
.question{
    flex-direction: column;
    display: flex;
    gap: 1.1rem;
    width: 60rem;
    padding: 5rem;
}

.faq__container{
    padding-top: 4rem;
}
.question__section-item{
    display: flex;
    flex-direction: column;
    background-color: white;
    width: 40rem;
    /* border: 1px solid #cfcfcf; */
    
}
.question__section-item p{
    margin-top: 0rem;
    width: 35rem;
    padding-left: 1rem;
    padding-bottom: 1rem
    
}
.ques-item{
    display: flex;
    align-items: center;
    justify-content: flex-start;
    background-color: white;
    gap: 4rem;
    padding: 1rem;
    width: 40rem;
    border-radius: 1rem;
    

   
    
}

.ques-item h1{
    font-weight: 800;
font-size: 1rem;
line-height: 22px;
/* identical to box height, or 100% */
width: 34rem;



color: #000000;
}
.ques-item >img{
    margin-left: 4rem;
    height: 1rem;
}
.question__section{
    display: flex;
    align-items: center;
    justify-content: center;
}
.question-right__Section {
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: white;
   
    flex-direction: column;
    padding: 3rem;
    width: 40rem;
    gap: 2rem;
    margin-right: 5rem;
    border-radius: 1rem;
    border: 1px solid #a8a2a2;
}
.question-right__Section h1{
    text-align: center;
    font-size: 1.6rem;
}
.question-right__Section p{
    text-align: center;
    font-size: 1rem;
}

.bbt{
    height: 4rem;
    width: auto;
}


@media screen and (max-width: 976px){
    .faq__top >h1 {
        margin-left: 0rem;
        width: 100%;
        font-weight: 900;
        font-size: 34px;
        line-height: 90px;
        
    }
    
    .faq__top img{
       height: 3rem;
    }
    .question{
        padding: 2rem;
        width: 100%;

    }
    .question__section {
        flex-direction: column;
        align-items: flex-start;
    }
    .question-right__Section{
        margin-left: 2rem;
        width: 80%;
        margin-right: 0rem;
    }
    .ques-item{
        width: 100%;
    }
    .question__section-item{
        width: 100%;
    }
    .question__section-item p {
        width: 100%;
    }
    .ques-item h1{
        width: 100%;
    }
}

@media screen and (max-width: 400px){
    .faq__top >h1{
        width: 100%;
        margin-left: 0rem;
        
        font-weight: 900;
        font-size: 20px;
        line-height: 25px;
    }
    .ques-item {
        width: 100%;
        gap: 0rem;
    }
    .ques-item h1{
        font-size: 12px;

    }
    .question__section-item p{
        font-size: 11px;
    }
    .question {
        padding: .5rem;
        width: 100%;
    }
    .bbt {
        width: 100%;
        height: auto;
    }
    .question-right__Section h1 {
        text-align: center;
        font-size: 12px;
    }
    .faq__container{
        padding-top: 0px;
    }

}
@media screen and (min-width: 980px){
    .faq__top >h1{
        width: 100%;
        margin-left: 3rem;
    }
    .question{
        width: 100%;
        gap: 2rem;
       
    }
    .question-right__Section{
        width: 100%;
        margin-right: 0rem;
        padding-top:2rem ;
        padding-bottom:2rem ;
        padding-left:2rem ;
    }
    .question__section{
        gap: 0rem;
        
    }
    .faq__container{
        padding: 0rem;
    }
    .bbt{
        width: 40%;
        height: auto;
    }
}


