.navbar__container{
    display: flex;
    align-items: center;
    justify-content: space-around;
   padding-top: 2rem;
    background: linear-gradient(292.12deg, #62BADA -35.02%, #C9E7F2 78.96%);
}
.logo p{
    font-family: 'Manrope';
font-style: normal;
font-weight: 900;
font-size: 20px;
line-height: 25px;
text-transform: uppercase;

color: #000000;
}
.links__lists ul{
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 3rem;

}
.links__lists ul li{
    list-style: none;
    font-size: 1.1rem;
    font-family: 'Manrope';
    font-weight: 700;
    position: relative;
    
}
.navbar__right-section{
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 2rem;
}
.navbar__right-section a{
    font-size: 1.1rem;
    font-family: 'Manrope';
    font-weight: 700;
    text-decoration: none;
    color: black;
}
.btn{
    background: #4F46BA;
    font-size: 1.1rem;
    font-family: 'Manrope';
    padding: .3rem 1.1rem .3rem 1.1rem;
    border: none;
    color: white;
    cursor: pointer;
}
.template img{ 
    height: .6rem;
    position: absolute;
    top: .5rem;
    left: 5rem;

}
.product img{
    height: .6rem;
    position: absolute;
    top: .5rem;
    left: 4.2rem;
    
}

@media screen and (max-width: 400px)
{
.links__lists ul li {
    font-size: .3rem;
}

.logo p {
    font-size: 12px;
}
}


