.footer__setion{
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    gap: 2rem;
}
.footer--top{
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    gap: 7rem;

}
.footer__items{
    display: flex;
    align-items: flex-start;
    justify-content: center;
    flex-direction: column;
    gap: 1rem;

}
.footer__items h1{
    font-weight: 700;
font-size: 21px;
line-height: 28px;


letter-spacing: -0.02em;



}
.footer__items >p{
    font-size: 16px;
line-height: 28px;
width: 20rem;

letter-spacing: -0.02em;

color: #757095;
opacity: 0.8;
margin-top: 0rem;

}
.footer__items img{
    height: .9rem;

}
.footer__items ul{
    display: flex;
    align-items: flex-start;
    justify-content: center;
    flex-direction: column;
    gap: 1rem;

}
.footer__items ul li{
    list-style: none;

}
.footer__items ul li a{
    text-decoration: none;
    font-weight: 500;
font-size: 16px;
line-height: 24px;


color: #181433;

}
.search input{
    width: 300px;
height: 42px;
font-weight: 500;
font-size: 16px;
line-height: 26px;
letter-spacing: -0.02em;

color: #181433;
border: none;
padding: 1rem;


background: #EEEEFF;
}
.s-btn{
    font-weight: 500;
font-size: 16px;
line-height: 26px;
background-color: #3734A9;;
color: #FFFFFF;
width: 120px;
height: 42px;
border: none;
cursor: pointer;
}

.footer__setion hr{
    width: 90%;
}
.footer__setion >p{
    font-weight: 500;
font-size: 16px;
line-height: 26px;
text-align: center;
letter-spacing: -0.02em;

color: #181433;
margin-top: -3rem;
margin-bottom: 2rem;

}

@media screen and (max-width: 976px){
    .footer--top{
        gap: 1rem;
    }
    .footer__items >p{
        width: 70%;
        font-size: 12px;
        line-height: 1.1rem;
        margin-top: 1rem;
    }
    .footer__setion{
        gap: 1rem;
        padding: 1rem;
    }
    .footer__items h1 {
        font-weight: 700;
        font-size: 15px;
    }
    .footer__items{
        gap: 0rem;
    }
    .footer__items ul li a {
        text-decoration: none;
        font-weight: 500;
        font-size: 12px;
        line-height: 0px;
        color: #181433;
    }
    .footer__items ul{
        gap: 0rem;
    }
    .search input {
        width: 57%;
        height: 36px;
        
        font-size: 12px;
       
       
       
    }
    .s-btn {
       
        font-size: 13px;
      
       
       
        width: 120px;
        height: 35px;
      
    }
    .footer__setion >p{
        font-size: 12px;
        margin-top: -2rem;
    }
    
}


@media screen and (max-width: 600px){
    .footer--top {
        flex-direction: column;
    }
    .footer__setion{
        margin-left: 0rem;
    }
    .footer__setion hr{
        width: 100%;
    }

}
@media screen and (min-width: 980px){
   
    .footer__setion{
        padding: 2rem;
    }
    .footer__items >p {
        width: 100%;
    }
    .search{
        width: 100%;
    }
    
}