.main__container{
    display: flex;
    align-items: flex-end;
    justify-content: space-between;
    padding: 4rem 0 4rem 0;
    
   

background: linear-gradient(292.12deg, #62BADA -35.02%, #C9E7F2 78.96%);

}
.main__container--right{
    display: flex;
    align-items: flex-start ;
    justify-content: flex-start;
    flex-direction: column;
    gap: 2rem;
    padding-left: 3.4rem;
    margin-left: 5.5rem;
    
    
}
.main__container--right h1{
    font-size: 2.5rem;
    font-weight: 900;
    letter-spacing: -0.03em;

color: #000000;
}
.main__container-button{
    display: flex;
    align-items: center;
    justify-content: center;  
    gap: 2rem;  
}
.main__container-button >button{
   background: #3734A9;
border-radius: 47px;
padding: 1rem 2rem 1rem 2rem;

}
.btn2, .btn1{
    display: flex;
    align-items: center;
    justify-content: center;
    gap: .3rem;
}
.main__container-button img{
   height: 3rem;

}
.btn1 p{
    cursor: pointer;
}
.btn2 img{
    height: 1rem;

}
.btn2 p{
    font-size: .8rem;
}
.main__container-button-1{
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 1rem;
    position: relative;
}

.main__container--left{
   
    /* position: absolute;
    top: 6.5rem;
    font-size: smaller;
    left: 57rem */
    position: relative;
    

}
.forth-image{
    width: 15.8rem;
    height: auto;
    position: absolute;
    top: -26rem;
    left: -14rem;

    
}
.birds{
    top: 105px;
    right: 0;
    position: absolute;
}

.birds1{
    position: absolute;
    top: -2rem;
    left: -9rem;
    /* background-image: url('./src/assets/24.svg'); */
    /* height: 10rem;
    width: auto; */

}
.ellipse img{
    position: absolute;
    right: 0px;
    bottom: 900px;
    height: 7rem;
    /* transform: rotate(4deg); */
}




