.cta__section{
    background-color: #3734A9;
    margin: 5rem;
    border-radius: 2rem;
    padding: 3rem;
    padding-left: 5rem;
    padding-top: 10rem;
}

.cone__container{
    /* position: relative; */
    position: absolute;
    top: -31px;
    left: 271px;
}
/* .cone__container1{
    position: absolute;
    top: 0px;
    left: 0px;

} */
.cone{
     
    height: 2.5rem;
    width: 2.5rem;
    background-color: #FDBC64;
    border-radius:  1rem 1rem 20rem 1rem;
    margin-left: 0rem;
    


}
.cone1{
    background-color: #E8F2FF;;
}
.cone2{
    background: #E8F2FF;
    height: 1.5rem;
    width: 1.5rem;
   
    border-radius:  1rem 1rem 20rem 1rem;
}
.cone3{
    background-color:  #00E1F0;
    /* position: absolute; */
    /* top: 4rem;
    right: 50rem; */
    height: 2rem;
    width: 2rem;
    margin-left: 1rem;
}
.cone4{
    margin-left: 1.5rem;

}

.cta__text{
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
   
}
.h1_ctn{
   
font-weight: 800;
font-size: 50px;
line-height: 61px;
/* or 109% */

text-align: center;
letter-spacing: -0.03em;

color: #fcfcfc;
width: 50rem;

}
.dot{
    position: absolute;
    top: -24px;
    left: 130px;
}
.spiral1{
    position: absolute;
    top: -74px;
    right: 0px;
}
.cta__button{
    display: flex;
    margin-top: 5rem;
    align-items: center;
    justify-content: space-between;
    position: relative;
}
.btn_ctn{
    background: #FF7F5C;
border-radius: 10px;
padding-left: 6rem;
padding-right: 6rem;
padding: 1rem;

font-weight: 700;
font-size: 18px;
line-height: 30px;
/* identical to box height, or 167% */

text-align: center;
letter-spacing: -0.02em;

color: #FFFFFF;
margin-left: 0rem;

}

@media screen and (max-width: 976px){
    .h1_ctn{
        font-size: 25px;
        line-height: 31px;
        margin-top: -5rem;
        width: 20rem;
    }
    .dot {
        position: absolute;
        top: -90px;
    left: 97px;
        height: 6rem;
    }
    .spiral1 {
        position: absolute;
        top: -124px;
        right: 0px;
        width: 4rem;
    
    }
    .spiral img{
        width: 8px;
    }
    .cone__container{
        top: -27px;
        left: 72px;
    }
    .cta__section{
        margin: 1rem;
        /* padding: 2rem; */
    }
    .content__section-left{
        width: 100%;
        height: auto;
    }
    
}
@media screen and (max-width: 800px){
    .cone__container{
        display: none;
        
    }
    .cone__container1{
        display: none;

    }
    .h1_ctn{
        font-size: 18px;
        width: 100%;
    }
    .btn_ctn{
        margin-left: 2rem;
        font-size: 9px;
    line-height: 0px;
    }
    .cta__button {
        align-items: center;
        justify-content: center;
        margin-top: 1rem;
    }
    .spiral img{
        margin-left: -9rem;
    }
}

@media screen and (max-width: 400px){

.spiral img {
    margin-left: -7rem;
}
.btn_ctn {
    margin-left: -3rem;
}
.h1_ctn {
    font-size: 13px;
    margin-top: -12rem;
    line-height: 1rem;
}
.dot {
    position: absolute;
    top: -136px;
    left: -23px;
    height: 4rem
}
.spiral1 {
    position: absolute;
    top: -144px;
    right: 0px;
    width: 4rem;
}
.cta__button{
    margin-top: -3rem;
}
}

@media screen and (min-width: 980px){

    .cta__section{
        margin: 2rem;
    }
}