.feature__section{
    background: #C9E7F2;
    padding: 6rem;
    position: relative;
    
    
    

}
.text{
    display: flex;
    align-items: flex-start;
    justify-content: center;
    flex-direction: column;
    gap: 1.7rem;
}
.text h1{
    font-weight: 800;
    font-size: 50px;
    line-height: 66px;
    
    letter-spacing: -0.03em;
    
    color: #000000;

}
.text p{
    font-weight: 500;
font-size: 16px;
line-height: 29px;
letter-spacing: -0.02em;
}
.feature__section--item{
    display: flex;
   
    align-items: flex-start;
    justify-content: space-between;
    /* gap: 4rem; */
    margin-top: 5rem;
    
}
.items{
    background: #FFFFFF;
    padding: 2rem;
    display: flex;
    align-items: flex-start;
    justify-content: flex-start;
    flex-direction: column;
    gap: 2rem;
    border-radius: 1rem;
    width: 23rem;

}
.items h1{
    font-size: 22px;
line-height: 35px;
/* or 159% */

letter-spacing: -0.03em;

color: #000000;
}
.items p{
    font-weight: 500;
font-size: 15px;
line-height: 26px;
/* or 173% */

letter-spacing: -0.02em;


}
.item2{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
    gap: 2rem;
}
.subItem{
    background-color: white;
    width: 25rem;
    padding: 39px;
    border-radius: 1rem;
}
.subItem h1{
    font-weight: 800;
font-size: 22px;
line-height: 35px;


letter-spacing: -0.03em;

color: #000000;
}

.ramdom img{
    position: absolute;
    bottom: 4rem;
    left: 0rem;
}

@media screen and (max-width: 976px){
    .feature__section--item{
        flex-direction: column;
        gap: 2rem;
        flex-wrap: wrap;
        align-items: center;
        justify-content: center;
    }
    .feature__section{
        padding: 4rem;
    }
    .items{
        width: 100%;
    }
    .subItem {
       width: 100%;
       padding: 57px;
    }
    .text h1 {
        font-size: 30px;
    }
    .feature__section {
        padding: 2rem;
    }
    .item2{
        justify-content: flex-start;
    }
    .ramdom img {
        height: 3rem;
    }
}
@media screen and (max-width: 600px){

.items {
    width: 100%;
    /* margin-top:-3rem ; */
}
.text p {
    width: 100%;
}
.feature__section--item{
    align-items: flex-start;
    margin-top: 1rem;
}
.subItem {
    width: 100%;
}
.text h1 {
    font-weight: 800;
    font-size: 32px;
    line-height: 2.5rem;
    letter-spacing: -0.03em;
    color: #000000;
}
}

@media screen and (max-width: 400px){
    .feature__section {
        padding: 2rem;
    }

    .items {
        width: 100%;
        gap: 1rem ;
        /* margin-top:-3rem ; */
    }
    .feature__section--item{
        align-items: flex-start;
        margin-top: 1rem;
    }
    .subItem {
        width: 100%;
        padding: 1rem;

    }
    .text h1 {
        font-weight: 800;
        font-size: 16px;
        line-height: 2.5rem;
        letter-spacing: -0.03em;
        color: #000000;
    }
    .ramdom img {
       
        height: 1rem;
    }
    .text{
        gap: 0rem;
    }
    .text p {
        font-size: 12px;
        line-height: 20px;
    }
    .items h1{
        font-size: 16px;
        line-height: 23px;
    } 
    .items p{
        font-size: 12px;
        line-height: 20px;
    }
    .subItem h1{
        font-size: 16px;
        line-height: 25px;
       
    }
    }
    @media screen and (min-width: 980px){
        .feature__section{
            padding: 2rem;
        }
        .feature__section--item {
            gap: 4rem;
            flex-wrap: wrap;
            justify-content: center;
        }
    }