.value__section{
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    flex-wrap: wrap;
    margin-top: 4rem;
}
.value--first{
    display: flex;
    align-items: flex-start;
    justify-content: flex-start;
    flex-direction: column;
    width: 20rem;
    background-color: #FFFFFF;
    padding: 1rem;
    border-radius: 1rem;
    border: 1px solid #DBE4E9;
    
}
.value--first p{
    font-size: 1rem;
    font-weight: 900;
}
.value--child{
    flex-direction: column;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 2rem;
}
.value--child p{
    font-weight: 500;
font-size: 14px;
line-height: 26px;
/* or 186% */

text-align: center;
letter-spacing: -0.02em;

}
.value--child img{
    height: 2rem;
}
.value--child h1{
    font-weight: 800;
font-size: 35px;
line-height: 34px;


text-align: center;
letter-spacing: -0.03em;

color: #000000;
}

@media screen and (max-width: 976px){
    .value__section{
        gap: 2rem;
    }
    .value--child h1{
        font-size: 2rem;
    }
    .value--child {
        
        gap: 2rem;
    }
    .value--child p {
        font-weight: 500;
        font-size: 13px;
        line-height: 21px;
        text-align: center;
        letter-spacing: -0.02em;
    }   
     
}

@media screen and (max-width: 600px){
    .value__section{
        gap: 2rem;
        flex-direction: column;
        padding: 2rem;
    }
    .value--first{
        width: 100%;
    }
}
@media screen and (max-width: 400px){
    .value__section{
        gap: 2rem;
        flex-direction: column;
        padding: 2rem;
        margin-top: 0rem;
    }
    .value--first{
        width: 100%;
    }
    .value--child h1 {
        font-size: 12px;
    }
    .value--child {
        gap: 0rem;
    }
}

