.testimonial{
    display: flex;
    align-items: center;
    justify-content: center;
    /* width: 50rem; */
    padding: 1rem 0rem 1rem 1rem;
    
    

}
.testimonial__section{
    padding: 5rem;
    padding-right:0rem ;
}
.testinomial__container{
    display: flex;
    align-items: center;
    justify-content: space-between;
    
}
.testimonial--image img{
    height: 20rem;

    
}
.rectangular1{
    background-color: white;
    width: 28rem;
   

}
.section__testimonial-right{
    /* margin-left: 5rem; */
    display: flex;
    flex-direction: column; 
     border: 1px solid #CFCFCF;
     align-items: flex-start;
     margin-top: 3rem;
     position: relative;
}
.section__testimonial-right >img{
    height: 2rem;
}
.first{
    font-weight: 600;
font-size: 25px;
line-height: 36px;
/* or 144% */

letter-spacing: -0.02em;

color: #000000;
}
.second{
    font-size: 20px;
line-height: 22px;
/* identical to box height, or 110% */

letter-spacing: -0.02em;

color: #000000;

}
.test-p{
    width: 35rem;
}
.section__testimonial-right >img{
    position: absolute;
    top: -13px;
    left: 27px;

}

@media screen and (max-width: 976px){
    .testinomial__container{
        flex-direction: column;
    }
    .testimonial__section{
        padding: 2rem;
    }
    .testimonial {
        width: 100%;
        padding: 0;
        padding-left: 2rem;
    }
    .first {
        font-weight: 600;
        font-size: 17px;
        line-height: 29px;
        letter-spacing: -0.02em;
        color: #000000;
    }
    .second {
        font-size: 13px;
        line-height: 22px;
        letter-spacing: -0.02em;
        color: #000000;
    }
    .testimonial--image img {
        height: 20rem;
        width: 100%;
    }
    
}
@media screen and (max-width: 600px){
    .testimonial {
        width: 100%;
        align-items: flex-start;
        justify-content: flex-start;
        flex-direction: column;
    }
    .text p{
        width: 100%;
    }
    .rectangular1{
        width: 100%;
    }

    }
@media screen and (max-width: 400px){
    .testimonial {
      flex-direction: column;
      width: 100%;
      /* margin-left: 4rem; */
      margin-top: 1rem;
    }

    .rectangular1 {
        background-color: white;
        width: 100%;
    }
    .first{
        font-size: 12px;
        line-height: 20px;
    }

    }

    @media screen and (min-width: 980px){
        .rectangular1 {
            width: 100%;
        }
        .first{
            font-size: 16px;
        }
        .second{
            font-size: 16px;
        }
        .testimonial--image img{

            width: 100%;
            height: auto;
        }
        .testimonial{
            padding: 0rem;
            padding-left: 1rem;
        }
        .testinomial__container{
            gap: 2rem;
        }
    }