.section__heading{
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 30rem;
    padding-top: 4rem;
    padding-bottom: 2rem;
}
.section__heading-left{
    width: 25rem;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    flex-direction: column;
    gap: 2rem;
}
.section__heading-left h1{
    font-size: 3rem;

}
.section__heading-left p{
    font-size: 16px;
   line-height: 1.8rem;
}
.section__heading-right{
    display: flex;
    align-items: flex-start;
    justify-content: flex-start;
    flex-direction: column;
    width: 23rem;
    position: relative;
}
.rectangular{
    background-color: #C4C4C4;
    padding: 2rem;
    border-radius: 1rem;
}
.rectangular p{
    font-weight: 600;
    font-size: 15px;
    line-height: 22px
}
.section__heading-right> img{
    height: 2rem;
    position: absolute;
    top: -17px;
    left: 19px;
}

@media screen and (max-width: 976px) {

    .section__heading {
        gap: 0rem;
   padding: 2rem;
    flex-wrap: wrap;
    justify-content: flex-start;
    }
    .section__heading-left h1 {
        font-size: 2rem;
    }
    .section__heading-left {
       
        gap: 1rem;
    }
    /* .rectangular{
        width: 100%;
    } */
   
}
@media screen and (max-width: 800px) {
    .section__heading {
        gap: 2rem;
    padding-top: 2rem;
    padding-bottom: 1rem;
    flex-wrap: wrap;
    align-items: flex-start;
    }
    .section__heading-right {
        
        width: 100%;
       
    }

}
@media screen and (max-width: 400px){
    .section__heading{
        padding: 2rem;
    }
    .section__heading-left h1 {
        font-size: 1.3rem;
    }
    .section__heading-left p {
        font-size: 12px;
        line-height: 1rem;
    }
    .rectangular p {
        font-weight: 600;
        font-size: 12px;
        line-height: 18px;
    
    }
    .rectangular {
        background-color: #C4C4C4;
        padding: 1rem;
        border-radius: 1rem;
    }
}
@media screen and (min-width: 980px){
    .section__heading{
        justify-content: space-around;
        gap: 0rem;
        padding: 0rem;
    }
}