.logo__container{
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    /* margin-top: 2rem; */
    background-color: #F5F6F8;
    padding: 4rem;
    gap: 2rem;
    
}
.logo__container p{
    font-style: normal;
font-weight: 500;
font-size: 20px;
line-height: 30px;
/* identical to box height */

text-align: center;

color: #000000;
}
.logo__image{
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 4rem;
    flex-wrap: wrap;
}


@media screen and (max-width: 976px) {
    .logo__image{
        gap: 2rem;
        align-items: center;
        justify-content: center;
    }
    .logo__container p{
        font-size: 12px;
    }
}

@media screen and (max-width: 600px) {
    
    .logo__container p{
        font-size: 12px;
    }
    .logo__container{
        padding: 1.5rem;
    }
    .logo__image img{
        height: 1rem;
    }
}